import React from "react";
import styled from "styled-components";
import Link from "gatsby-link";

import { Github } from "styled-icons/fa-brands/Github";
import { LinkedinIn } from "styled-icons/fa-brands/LinkedinIn";
import { shine } from "./Effect";

const FixedHeader = styled.nav`
  position: ${props => (props.fixed ? "fixed" : "")};
  background-color: #100e12;
  padding: 25px 0;
  color: #ffffff;
  box-sizing: border-box;
  top: 0;
  right: 0;
  left: 0;
  z-index: 1030;
  align-items: center;
  transition: top 0.3s;

  @media all and (max-width: 600px) {
    padding: 20px 15px;
    height: 75px;
  }
`;

const HeaderInner = styled.div`
  max-width: 1200px;
  text-align: center;
  margin: 0 auto;
  padding: 0 15px;
  display: flex;
  align-items: center;
  justify-content: space-between;
`;

const HeaderMenu = styled.ul`
  font-size: 16px;
  margin: 0;
  line-height: 1.5;
  box-sizing: border-box;
  outline: none;
  list-style: none;
  color: rgba(0, 0, 0, 0.65);
  line-height: 0;
  transition: background 0.3s, width 0.2s;
  padding: 0;

  @media all and (max-width: 600px) {
    line-height: 1;
    float: none;
    font-size: 13px;
  }
`;

const MenuItem = styled.li`
  display: inline-block;
  color: #ffffff;
  margin: 0;
`;

const LogoLinked = styled(Link)`
  color: #ffffff;
  text-decoration: none;
`;

const StyledLink = styled(LogoLinked)`
  padding: 0.7em calc(0.7em * 1.2);
  position: relative;

  :before {
    content: "";
    position: absolute;
    content: "";
    bottom: -3px;
    left: calc(0.7em * 1.2);
    right: calc(0.7em * 1.2);
    height: 3px;
    background: linear-gradient(to right, #f09819, #ff512f);
    transform: scaleX(0);
    transition: all 0.3s ease-in-out 0s;
  }

  :hover:before {
    visibility: visible;
    transform: scaleX(1);
  }

  :hover {
    background: linear-gradient(to right, #f09819, #ff512f);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }

  &.selected {
    color: #1c46f2;
    background: linear-gradient(to right, #f09819, #ff512f);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    opacity: 1;
  }
  &.selected:before {
    visibility: visible;
    transform: scaleX(1);
  }
`;
const SocialLink = styled.a`
  padding: 0.7em calc(0.7em * 1.2);
  position: relative;

  color: inherit;
  transition: 200ms;
  transition-timing-function: ease-in-out;

  :hover {
    color: #f09819;
  }
`;
const StyledGithubIcon = styled(Github)`
  @media all and (max-width: 600px) {
    width: 22px;
  }
`;

const StyledLinkedinInIcon = styled(LinkedinIn)`
  @media all and (max-width: 600px) {
    width: 22px;
  }
`;
export default class Header extends React.Component {
  constructor(props) {
    super(props);

    this.state = {};
    this.handleScroll = this.handleScroll.bind(this);
  }
  handleScroll() {
    this.setState({ scroll: window.scrollY });
  }
  componentDidMount() {
    const el = document.querySelector("nav");
    this.setState({ top: el.offsetTop, height: el.offsetHeight });
    window.addEventListener("scroll", this.handleScroll);
  }

  componentDidUpdate() {
    this.state.scroll > this.state.top
      ? (document.body.style.paddingTop = `${this.state.height}px`)
      : (document.body.style.paddingTop = 0);
  }
  render() {
    console.log(this.state.scroll > this.state.top);
    return (
      <FixedHeader fixed={this.state.scroll > this.state.top}>
        <HeaderInner>
          <HeaderMenu>
            <MenuItem>
              <StyledLink to="/" activeClassName="selected">
                Home
              </StyledLink>
            </MenuItem>
            <MenuItem>
              <StyledLink
                to="/blog/"
                activeClassName="selected"
                state={{
                  pleasant: "reasonably"
                }}
              >
                Notes
              </StyledLink>
            </MenuItem>
            <MenuItem>
              <StyledLink
                to="/portfolio/"
                activeClassName="selected"
                state={{
                  pleasant: "reasonably"
                }}
              >
                Portfolio
              </StyledLink>
            </MenuItem>
          </HeaderMenu>

          <HeaderMenu>
            <MenuItem>
              <SocialLink href="https://github.com/MagicMike90">
                <StyledGithubIcon size={32} />
              </SocialLink>
            </MenuItem>
            <MenuItem>
              <SocialLink href="https://www.linkedin.com/in/michael-luo-10066110a/">
                <StyledLinkedinInIcon size={32} />
              </SocialLink>
            </MenuItem>
          </HeaderMenu>
        </HeaderInner>
      </FixedHeader>
    );
  }
}
